* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.app-container {
  padding: 14px;
  padding-top: 0px;
}

.nav-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: -16px;
}

.subscribe {
  font-family: 'Roboto';
  font-size: 9px;
  color: #737373;
}

.large {
  font-size: 20px;
}

.search-icon {
  color: #737373;
  margin-right: 10px;
  margin-bottom: -5px;
  width: 16px;
}

.signUp-button {
  background-color: white;
  border: 1px solid #737373;
  border-radius: 2px;
  padding: 4px;
  color: #737373;
  font-family: 'Roboto';
  font-size: 9px;
  outline: none;
  cursor: pointer;
}

.hr-line {
  background-color: #f5f5f5;
}

.categories {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: -5px;
}

.category {
  color: #737373;
  font-family: 'Roboto';
  font-size: 6px;
}

.title-card {
  width: 100%;
  background-color: #303030;
  border-radius: 3px;
  margin-top: 5px;
  padding: 18px;
  padding-top: 8px;
}

.card-heading {
  color: white;
  font-size: 18px;
  font-style: italic;
  width: 60%;
}

.card-description {
  color: white;
  font-family: 'Roboto';
  font-size: 8px;
  width: 60%;
}

.continue-link {
  color: white;
  font-family: 'Roboto';
  font-size: 9px;
  font-weight: 600;
  text-decoration: none;
}
@media screen and (min-width: 576px) {
  .subscribe {
    font-size: 12px;
  }

  .large {
    font-size: 24px;
  }

  .search-icon {
    width: 20px;
  }

  .signUp-button {
    font-size: 12px;
  }

  .category {
    font-size: 10px;
  }

  .card-heading {
    font-size: 20px;
  }

  .card-description {
    font-size: 10px;
  }

  .continue-link {
    font-size: 10px;
  }
}
@media screen and (min-width: 768px) {
  .subscribe {
    font-size: 16px;
  }

  .large {
    font-size: 32px;
  }

  .search-icon {
    width: 22px;
  }

  .signUp-button {
    font-size: 16px;
  }

  .category {
    font-size: 13px;
  }

  .card-heading {
    font-size: 28px;
    width: 40%;
  }

  .card-description {
    font-size: 12px;
    width: 40%;
  }

  .continue-link {
    font-size: 12px;
  }
}
@media screen and (min-width: 992px) {
  .subscribe {
    font-size: 20px;
  }

  .large {
    font-size: 36px;
  }

  .search-icon {
    width: 24px;
  }

  .signUp-button {
    font-size: 20px;
  }

  .category {
    font-size: 16px;
  }

  .card-heading {
    font-size: 34px;
    width: 40%;
  }

  .card-description {
    font-size: 14px;
    width: 40%;
  }

  .continue-link {
    font-size: 14px;
  }
}
